*{
  font-family: 'Mukta', sans-serif;
}

.Home {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #ffffff;
  min-height: 48vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #272343;
}

.Pricing-header {
  background-color: #ffffff;
  min-height: 24vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #272343;
}

.About-header {
  background-color: #ffffff;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #272343;
}

.Faq-header {
  background-color: #ffffff;
  min-height: 25vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #bae8e8;
}

.Contact-header {
  background-color: #ffffff;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #bae8e8;
}

.App-link {
  color: #61dafb;
}

.contact {
  text-align: center;
  font-family: 'Mukta', sans-serif;
  color: #272343;
}

.home {
  justify-content: center;
  text-align: center;
  font-family: 'Mukta', sans-serif;
  color: #272343;
}

.onImageAbout{
  justify-content: center;
  text-align: center;
  font-family: 'Mukta', sans-serif;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #272343;
}

.centerBlock {
  margin-top: 10em;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.bigPicture {
  height: 100%;
  width: 100%;
  background-color: #e3f6f5;
}

.onImage {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  
}

.containerImg {
  position: relative;
  text-align: center;
  color: white;
}

.slide-header-left {
 width: 66%;
 background-color: #272343;
 color: #e3f6f5;
 height: 2.5em;
 padding-top: .5em;
 font-size: 2em;
 left: 0;
}

.slide-header-right {
  width: 66%;
  background-color: #272343;
  color: #e3f6f5;
  height: 2.5em;
  padding-top: .5em;
  font-size: 2em;
  right: 0;
  position: absolute;
 }

/*****************************************
  Defining C2C Color Palette
    https://colorhunt.co/palette/163855
    The reason for !important is to take
    priority over react-bootstrap styles
*****************************************/

.c2cIndigo {
  background-color: #272343 !important;
}

.c2cWhite {
  background-color: #ffffff !important;
}

.c2cSky {
  background-color: #e3f6f5 !important;
}

.c2cMint {
  background-color: #bae8e8 !important;
}

